.results-chart__section {
  padding-top: 40px;
  padding-bottom: 70px;

  @media screen and (min-width: 1170px) {
    padding-top: 136px;
    padding-bottom: 130px;
  }
}

.results-chart__container {
  position: relative;
}

.results-chart__title {
  color: #2e2e2e;
  text-align: center;
  font-family: Futura PT;
  font-size: 23px;
  font-style: normal;
  font-weight: 700;
  line-height: normal;
  margin-bottom: 10px;

  @media screen and (min-width: 1170px) {
    font-size: 40px;
    text-align: start;
    width: 386px;
    margin-bottom: 20px;
  }
}

.results-chart__description--mobile,
.results-chart__description--desktop {
  color: #979797;
  text-align: center;
  font-family: Inter;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 168.5%;
  /* 26.96px */
  margin-bottom: 40px;

  @media screen and (min-width: 1170px) {
    width: 566px;
    font-size: 20px;
    text-align: start;
    margin-bottom: 20px;
  }

  span {
    font-weight: 600;
  }
}

.results-chart__description--desktop {
  display: none;

  @media screen and (min-width: 1170px) {
    display: block;
  }
}

.results-chart__description--mobile {
  display: block;

  @media screen and (min-width: 1170px) {
    display: none;
  }
}

.results-chart__info {
  display: none;

  @media screen and (min-width: 1170px) {
    display: flex;
    position: relative;
  }

  .results-chart__icon-arrow {
    position: absolute;
    top: -2px;
    left: -4px;
  }

  .results-chart__info-text {
    display: flex;
    transform: rotate(-90deg);
    p {
      margin-top: 42px;
      color: #979797;
      font-family: Inter;
      font-size: 16px;
      font-style: normal;
      font-weight: 400;
      line-height: normal;
      text-transform: uppercase;
    }
  }

  .results-chart__wrap {
    position: absolute;
    top: 50%;
    left: 120px;
    transform: translateY(-50%);
  }

  .results-chart__label {
    color: #56be54;
    font-family: Inter;
    font-size: 16px;
    font-style: normal;
    font-weight: 700;
    line-height: 16px;
    /* 100% */

    position: absolute;
    top: 100%;
    transition: top 2000ms ease-in-out, left 2000ms ease-in-out;
  }
}

.results-chart__image-wrapper {
  position: relative;
  max-width: 290px;
  width: 100%;
  height: 270px;
  border-radius: 60px;
  background: #fffdfd;
  box-shadow: 0px 4px 25px 0px rgba(0, 0, 0, 0.09);

  @media screen and (min-width: 1170px) {
    border-radius: 111px;
    max-width: 475px;
    height: 430px;
    padding-bottom: 0;
    position: absolute;
    top: 51px;
    right: 16px;
  }
}

.results-chart__image-wrapper {
  background-repeat: no-repeat;
  background-position: 90px 0;
  background-size: 179px;

  @media screen and (min-width: 1170px) {
    background-position: 149px -10px;
    background-size: 292px;
  }
}

.results-chart__image-wrapper img {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: auto;
}

.results-chart__image-wrapper img {
  filter: blur(5px);
  transition: filter 400ms ease-in-out;
}

.results-chart__image-wrapper img.loaded {
  filter: blur(0);
}

.results-chart__button {
  color: #fff;
  font-family: Inter;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  margin-top: 40px;
  padding: 0;
  width: 100%;
  max-width: 170px;
  height: 46px;
  border-radius: 30px;
  background: #4768bd;
  border: 1px solid transparent;

  margin-left: auto;
  margin-right: auto;
  display: flex;
  align-items: center;
  justify-content: center;

  @media screen and (min-width: 1170px) {
    margin-top: 30px;
    margin-left: 0;
    margin-right: 0;
    font-size: 18px;
    height: 50px;
    cursor: pointer;
  }
}
