.working-units-section {
  margin-bottom: 70px;

  @media screen and (min-width: 1170px) {
    margin-bottom: 130px;
  }
}

.working-units-section__container {
  h2 {
    color: #2e2e2e;
    text-align: center;
    font-family: Futura PT;
    font-size: 23px;
    font-style: normal;
    font-weight: 700;
    line-height: normal;
    margin-bottom: 40px;

    @media screen and (min-width: 1170px) {
      text-align: start;
      font-size: 40px;
      margin-bottom: 50px;
    }
  }
}

.working-units-section__flex-wrapper {
  display: flex;
  flex-direction: column;

  @media screen and (min-width: 1170px) {
    flex-direction: row;
    justify-content: space-between;
  }
}

.working-units-section__artificial-intelligence,
.working-units-section__human {
  border-radius: 20px;
  background: #fff;
  box-shadow: 0px 4px 25px 0px rgba(0, 0, 0, 0.09);
  padding-top: 30px;
  padding-bottom: 30px;
  width: 100%;
  max-width: 290px;
  flex-shrink: 0;

  @media screen and (min-width: 1170px) {
    border-radius: 41px;
    padding-top: 40px;
    padding-bottom: 40px;
    padding-left: 40px;
    max-width: 440px;
  }

  h3 {
    color: #2e2e2e;
    text-align: center;
    font-family: Futura PT;
    font-size: 18px;
    font-style: normal;
    font-weight: 700;
    line-height: normal;
    margin-bottom: 20px;

    @media screen and (min-width: 1170px) {
      text-align: start;
      font-size: 24px;
    }
  }
}

.working-units-section__list {
  li {
    display: flex;
    align-items: center;
    flex-direction: column;

    @media screen and (min-width: 1170px) {
      flex-direction: row;
      align-items: flex-start;
    }
  }

  li:not(:last-child) {
    margin-bottom: 20px;

    @media screen and (min-width: 1170px) {
      margin-bottom: 25px;
    }
  }

  p {
    color: #979797;
    text-align: center;
    font-family: Inter;
    font-size: 16px;
    font-style: normal;
    font-weight: 600;
    line-height: normal;

    @media screen and (min-width: 1170px) {
      font-size: 18px;
      text-align: start;
    }
  }

  span {
    display: block;
    width: 15px;
    height: 15px;
    border-radius: 50%;
    background-color: #4768bd;
    flex-shrink: 0;
    margin-bottom: 10px;

    @media screen and (min-width: 1170px) {
      width: 10px;
      height: 10px;
      margin-top: 7px;
      margin-right: 10px;
      margin-bottom: 0;
    }
  }
}

.working-units-section__artificial-intelligence {
  margin-bottom: 40px;

  @media screen and (min-width: 1170px) {
    margin-bottom: 0;
  }
}

.working-units-section__image-wrapper {
  width: 100%;
  display: none;

  @media screen and (min-width: 1170px) {
    display: flex;
    align-items: center;
    justify-content: center;
  }

  img {
    min-width: 290px;
  }
}

.working-units-section__image-wrapper img {
  filter: blur(5px);
  transition: filter 400ms ease-in-out;
}

.working-units-section__image-wrapper img.loaded {
  filter: blur(0);
}
