.about-us-section {
    padding: 70px 0;

    @media screen and (min-width: 1170px) {
        padding: 130px 0;
    }
}

.about-us-section {
    text-align: center;

    @media screen and (min-width: 1170px) {
        text-align: start;
    }

    h2 {
        color: #2E2E2E;
        text-align: center;
        font-family: "Futura PT";
        font-size: 23px;
        font-style: normal;
        font-weight: 700;
        line-height: normal;
        margin-bottom: 10px;

        @media screen and (min-width: 1170px) {
            font-size: 40px;
            text-align: start;
            margin-bottom: 31px;
        }
    }

    p {
        color: #979797;
        text-align: center;
        font-family: Inter;
        font-size: 16px;
        font-style: normal;
        font-weight: 400;
        line-height: 168.5%;
        margin-bottom: 40px;
        /* 26.96px */

        @media screen and (min-width: 1170px) {
            font-size: 20px;
            line-height: 168.523%;
            text-align: start;
            margin-bottom: 30px;
            /* 33.705px */
        }
    }

    button {
        color: #fff;
        text-align: center;
        font-family: Inter;
        font-size: 16px;
        font-style: normal;
        font-weight: 400;
        line-height: normal;

        width: 100%;
        max-width: 192px;
        height: 46px;
        border-radius: 30px;
        background-color: var(--primary-color);
        border: transparent;
        cursor: pointer;

        &:hover,
        &:focus {
            background-color: var(--accent-color);
        }

        @media screen and (min-width: 1170px) {
            font-size: 18px;
            height: 50px;
        }
    }
}