.hero-section{
    padding-bottom: 30px;

    background-image: url(../images/index/backgraund-body-img-phone-mobile.png);
    background-repeat: no-repeat;
    background-position: center top 90px;
    background-size: contain;

    @media screen and (min-width: 370px) {
        background-image: url(../images/index/backgraund-body-img-phone-tablet.png);
        background-position: center top 134px;
        background-size: 370px;
    }

    @media screen and (min-width: 1170px) {
        background-image: url(../images/index/backgraund-body-img-phone-desktop.png);
        background-position: left 240px top -90px;
        background-size: 1000px;

        padding-bottom: 77px;
    }

    @media screen and (min-width: 1305px) {
        background-image: url(../images/index/backgraund-body-img-phone-desktop.png);
        background-position: left 79% top -90px;
    }

    @media screen and (min-width: 1540px) {
        background-image: url(../images/index/backgraund-body-img-phone-desktop.png);
        background-position: left 425px top -90px;
    }

    @media screen and (min-width: 1670px) {
        background-image: url(../images/index/backgraund-body-img-phone-desktop.png);
        background-position: left 63% top -90px;
    }
}

.hero-section__title {
    color: #2E2E2E;
    text-align: center;
    font-family: Futura PT;
    font-size: 23px;
    font-style: normal;
    font-weight: 700;
    line-height: normal;

    margin-bottom: 219px;

    @media screen and (min-width: 1170px) {
        width: 802px;
        font-size: 40px;
        margin-bottom: 40px;
        text-align: start;
    }
}