.our-services-section {
  margin-bottom: 70px;
  // outline: 1px solid red;

  @media screen and (min-width: 1170px) {
    margin-bottom: 130px;
  }
}

.our-services-section h2 {
  color: #2e2e2e;
  text-align: center;
  font-family: Futura PT;
  font-size: 23px;
  font-style: normal;
  font-weight: 700;
  line-height: normal;
  margin-bottom: 40px;

  @media screen and (min-width: 1170px) {
    font-size: 40px;
    text-align: start;
    margin-bottom: 70px;
  }
}

.our-services-section__flex-wrapper {
  // outline: 1px solid green;
  display: flex;
  flex-direction: column;

  @media screen and (min-width: 1170px) {
    position: relative;
    flex-direction: row;
    align-items: start;
  }
}

.our-services-section__flex-wrapper:not(:last-child) {
  margin-bottom: 40px;
  @media screen and (min-width: 1170px) {
    margin-bottom: 70px;
  }
}

//trading-bot
.trading-bot__icon {
  display: none;

  @media screen and (min-width: 1170px) {
    display: block;
  }
}

.trading-bot__icon-arrow {
  position: absolute;
  left: -4px;
  top: -4px;
}

.trading-bot__info-text {
  display: none;

  @media screen and (min-width: 1170px) {
    display: block;
    position: absolute;
    top: 54px;
    left: -36px;
    transform: rotate(90deg);

    p {
      color: #979797;
      font-family: Inter;
      font-size: 16px;
      font-style: normal;
      font-weight: 400;
      line-height: normal;
      text-transform: uppercase;
    }
  }
}

//trading-bot__container-wrapper
.trading-bot__container-wrapper {
  position: relative;
  display: flex;
  align-items: center;
  flex-direction: column;
  width: 100%;
  max-width: 290px;
  min-height: 783px;
  padding: 15px 10px 0 10px;
  border-radius: 60px 60px 16px 60px;
  background: #fff;
  box-shadow: 0px 4px 25px 0px rgba(0, 0, 0, 0.09);

  @media screen and (min-width: 1170px) {
    align-items: flex-start;
    width: 100%;
    max-width: 1096px;
    min-height: 787px;
    padding: 70px 70px 0 70px;
    border-radius: 111px 111px 34px 111px;
    background: #fff;
    box-shadow: 0px 4px 25px 0px rgba(0, 0, 0, 0.09);
    margin-left: auto;
  }

  h3 {
    width: 100%;
    max-width: 147px;
    color: #2e2e2e;
    text-align: center;
    font-family: Futura PT;
    font-size: 23px;
    font-style: normal;
    font-weight: 700;
    line-height: normal;
    margin-bottom: 10px;

    span {
      color: #4768bd;
    }

    @media screen and (min-width: 1170px) {
      max-width: 477px;
      font-size: 40px;
      margin-bottom: 20px;
    }
  }

  p {
    color: #979797;
    font-family: Inter;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 168.5%;
    text-align: center;
    margin-bottom: 20px;

    @media screen and (min-width: 1170px) {
      font-size: 20px;
      line-height: 168.523%;
      text-align: start;
      margin-bottom: 30px;
    }

    .tradestap {
      color: #2e2e2e;
      font-weight: 700;
    }

    .stap {
      color: #4768bd;
    }
  }

  p.trading-bot__text-mobile {
    display: block;
    @media screen and (min-width: 1170px) {
      display: none;
    }
  }

  p.trading-bot__text-desktop {
    display: none;
    @media screen and (min-width: 1170px) {
      display: block;
    }
  }

  button {
    color: #fff;
    text-align: center;
    font-family: Inter;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;

    width: 100%;
    max-width: 192px;
    height: 46px;
    border-radius: 30px;
    background-color: var(--primary-color);
    border: transparent;
    cursor: pointer;

    &:hover,
    &:focus {
      background-color: var(--accent-color);
    }

    @media screen and (min-width: 1170px) {
      font-size: 18px;
      // min-height: 50px;
      height: 50px;
    }
  }

  .trading-bot__image-wrapper {
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);

    width: 100%;
    max-width: 290px;

    @media screen and (min-width: 1170px) {
      max-width: 542px;
    }
  }

  .trading-bot__image-wrapper img {
    filter: blur(5px);
    transition: filter 400ms ease-in-out;
  }

  .trading-bot__image-wrapper img.loaded {
    filter: blur(0);
  }

  .trading-bot__img-cryptocurrency {
    position: absolute;
  }

  .trading-bot__img-cryptocurrency img {
    filter: blur(5px);
    transition: filter 400ms ease-in-out;
  }

  .trading-bot__img-cryptocurrency img.loaded {
    filter: blur(0);
  }

  //cryptocurrency img
  .trading-bot__img-cryptocurrency-bitcoin {
    left: 11px;
    bottom: 148px;
    width: 50.023px;
    height: 50.023px;

    @media screen and (min-width: 1170px) {
      left: 199px;
      bottom: 132px;
      width: 98.611px;
      height: 98.611px;
    }
  }

  .trading-bot__img-cryptocurrency-litecoin {
    right: 37px;
    bottom: 166px;
    width: 49.663px;
    height: 49.663px;

    @media screen and (min-width: 1170px) {
      right: 348px;
      bottom: 229px;
      width: 91.985px;
      height: 91.985px;
    }
  }

  .trading-bot__img-cryptocurrency-ethereum {
    right: 0;
    bottom: 80px;
    width: 50.769px;
    height: 50.769px;

    @media screen and (min-width: 1170px) {
      right: 199px;
      bottom: 20px;
      width: 87.841px;
      height: 87.841px;
    }
  }
}

