//for js ScrollMagicParallaxSections
.parallaxSection {
  // outline: 1px solid fuchsia;
  overflow: hidden;
}

//.trading-bot__imgBg
.parallaxSection .trading-bot__imgBg {
  // outline: 1px solid blue;
  position: absolute;

  width: 290px;
  min-height: 228px;
  bottom: 95px;
  left: 0;

  @media screen and (min-width: 1170px) {
    width: 1096px;
    min-height: 317px;
    bottom: 130px;
    left: 0;
  }

  background-repeat: no-repeat;
  background-position: center bottom;
  background-size: cover;

  // transition: all 100ms ease-out;
}

//tradestap-trading-team__imgBg
.parallaxSection .tradestap-trading-team__imgBg {
  // outline: 1px solid blue;
  position: absolute;
  overflow: hidden;
  width: 290px;
  min-height: 244px;
  bottom: 120px;
  left: 0;

  @media screen and (min-width: 1170px) {
    width: 1096px;
    min-height: 514px;
    bottom: 210px;
    left: 0;
  }

  background-repeat: no-repeat;
  background-position: center top;
  background-size: contain;

  // transition: all 100ms ease-out;
}

//money-in-trust__imgBg
.parallaxSection .money-in-trust__imgBg {
  // outline: 1px solid blue;
  position: absolute;
  width: 290px;
  min-height: 200px;
  bottom: 85px;
  left: 0;
  background-size: cover;

  @media screen and (min-width: 1170px) {
    width: 1096px;
    min-height: 445px;
    bottom: 185px;
    left: 0;
    background-size: contain;
  }

  background-repeat: no-repeat;
  background-position: center top;

  // transition: all 100ms ease-out;
}

//.tradestap-trading-team__imgBg::before
.parallaxSection .tradestap-trading-team__imgBg::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 50px; /* Высота верхней размытой границы */
  background: linear-gradient(
    to top,
    rgba(255, 255, 255, 0) 0%,
    rgba(255, 255, 255, 1) 100%
  );
  pointer-events: none; /* Предотвращает влияние на события мыши */

  @media screen and (min-width: 1170px) {
    height: 100px; /* Высота верхней размытой границы */
  }
}
