// menu-menu настройки скрола 
// когда открыто мобильно меню body скролить нельзя меню можно
.menu-container {
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    
    background-color: rgba(0, 0, 0, 0.50);
    z-index: 999;

    overflow: hidden;
    overflow-y: auto;

    transform: translateX(100%);

    opacity: 1;

    transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1), opacity 350ms cubic-bezier(0.4, 0, 0.2, 1),
        visibility 250ms cubic-bezier(0.4, 0, 0.2, 1);

    .menu-container-wrap {
        width: 100%;
        min-height: 100%;
        margin: auto;
    }
}

.menu-container.is-open {
    transform: translateX(0);
}

.menu-container.menu-container--is-hidden {
    opacity: 0;
    visibility: hidden;
    pointer-events: none;
}
