.marketplace-brands-section {
    background-color: #F5F5F5;
    padding-top: 20px;
    padding-bottom: 20px;

    @media screen and (min-width: 1170px) {
        padding-top: 45px;
        padding-bottom: 45px;
    }
}

.marketplace-brands__list {
    display: flex;
    flex-wrap: wrap;

    margin-left: -20px;
    margin-top: -20px;

    @media screen and (min-width: 1170px) {
        justify-content: space-between;
        margin-left: 0;
        margin-top: 0;
    }
}

.marketplace-brands__item {
    margin-left: 20px;
    margin-top: 20px;

    flex-basis: calc(100% / 2 - 20px);

    @media screen and (min-width: 1170px) {
        flex-basis: auto;
        margin-left: 0;
        margin-top: 0;
    }
}

.marketplace-brands__link {
    display: flex;
    align-items: center;
    justify-content: center;

    fill: #86aeb3;

    height: 40px;

    transition: fill 250ms cubic-bezier(0.4, 0, 0.2, 1);

    &:hover,
    &:focus {
        fill: #4768BD;
    }

    @media screen and (min-width: 1170px) {
        height: 50px;
    }
}

//icon
.marketplace-brands__icon-binance {
    width: 130px;
    height: 25.737px;
    @media screen and (min-width: 1170px) {
        width: 178.205px;
        height: 35.281px;
    }
}

.marketplace-brands__icon-bitmex {
    width: 130px;
    height: 20.598px;
    @media screen and (min-width: 1170px) {
        width: 163.37px;
        height: 25.885px;
    }
}

.marketplace-brands__icon-cryptocom {
    width: 130px;
    height: 26.034px;
    @media screen and (min-width: 1170px) {
        width: 144.624px;
        height: 28.962px;
    }
}

.marketplace-brands__icon-kucoin {
    width: 130px;
    height: 49.178px;
    @media screen and (min-width: 1170px) {
        width: 158.607px;
        height: 60px;
    }
}
