.animation-vector-section {
  display: none;

  @media screen and (min-width: 1170px) {
    overflow: hidden;
    display: flex;
    align-items: center;
    justify-content: center;
    padding-top: 15px;
    padding-bottom: 15px;
  }
}

.animation-vector-section__container {
  position: relative;
}

.box {
  border-radius: 50%;
  width: 25px;
  height: 25px;
  position: absolute;
  top: -12.5px;
  left: -12.5px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.box.color-1,
.box.color-2,
.box.color-3,
.box.color-4,
.box.color-5,
.box.color-6 {
  transition: background 500ms cubic-bezier(0.4, 0, 0.2, 1);
}

.box.color-1 {
  background: linear-gradient(90deg, #d443a2 -212.2%, #7121ff 1994.81%);
}

.box.color-2 {
  background: linear-gradient(90deg, #d443a2 -857.7%, #7121ff 2384.59%);
}

.box.color-3 {
  background: linear-gradient(90deg, #d443a2 -1343.11%, #7121ff 1891.74%);
}

.box.color-4 {
  background: linear-gradient(90deg, #d443a2 -1772.68%, #7121ff 1462.18%);
}

.box.color-5 {
  background: linear-gradient(90deg, #d443a2 -2245.42%, #7121ff 989.43%);
}

.box.color-6 {
  background: linear-gradient(90deg, #d443a2 -2699.17%, #7121ff 535.68%);
}
