.more-uniqueness-section {
  padding-bottom: 70px;

  @media screen and (min-width: 1170px) {
    padding-bottom: 130px;
  }
}

.more-uniqueness-section__container-title {
  margin-bottom: 40px;
}

.more-uniqueness-section__title {
  color: #2e2e2e;
  text-align: center;
  font-family: Futura PT;
  font-size: 23px;
  font-style: normal;
  font-weight: 700;
  line-height: normal;

  @media screen and (min-width: 1170px) {
    font-size: 40px;
    text-align: start;
    width: 447px;
  }
}

.more-uniqueness-section__wrap-video {
  position: relative;
  height: 323px;

  @media screen and (min-width: 1170px) {
    height: 450px;
  }
}

//animejs
.more-uniqueness-section__background-wrapper {
  overflow: hidden;
  height: 100%;
}

.more-uniqueness-section__background-img {
  // mobile
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;

  // desktop
  @media screen and (min-width: 1170px) {
    background-repeat: no-repeat;
    background-position: center center;
    background-size: cover;
  }

  height: 100%;

  transform: translateX(var(--x)) translateY(var(--y)) scale(var(--scale));
  transition: ease-out 0.7s;
}

.more-uniqueness-section__container {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 100%;
  max-width: 320px;
  padding-left: 15px;
  padding-right: 15px;
  transform: translateY(-50%) translateX(-50%);
  @media screen and (min-width: 1170px) {
    display: flex;
    align-items: center;
    max-width: 420px;
    padding-left: 0;
    padding-right: 0;
    transform: translateY(-50%) translateX(-585);
  }
}

.more-uniqueness-section__play-button {
  padding: 0;
  margin: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  border: transparent;
  border-radius: 50%;
  background-color: transparent;
  cursor: pointer;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 10px;

  @media screen and (min-width: 1170px) {
    margin-left: 0;
    margin-right: 0;
    margin-bottom: 0;
    margin-right: 30px;
  }
}

.more-uniqueness-section__play-icon {
  width: 80px;
  height: 80px;
}

.more-uniqueness-section__video-title {
  color: #fff;
  text-align: center;
  font-family: Futura PT;
  font-size: 23px;
  font-style: normal;
  font-weight: 450;
  line-height: normal;
  margin-bottom: 5px;

  @media screen and (min-width: 1170px) {
    font-size: 36px;
    text-align: start;
    margin-bottom: 10px;
  }

  span {
    font-weight: 700;
  }
}

.more-uniqueness-section__video-duration {
  color: #fff;
  font-family: Inter;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  text-align: center;

  @media screen and (min-width: 1170px) {
    font-size: 20px;
    text-align: start;
  }

  span {
    color: #fff;
    font-family: Inter;
    font-size: 16px;
    font-style: normal;
    font-weight: 600;
    line-height: normal;

    @media screen and (min-width: 1170px) {
      font-size: 20px;
    }
  }
}
