.benefits-with-us-section {
  background-color: #f5f5f5;
  padding-top: 40px;
  padding-bottom: 40px;
  position: relative;
  overflow: hidden;

  @media screen and (min-width: 1170px) {
    padding-top: 67px;
    padding-bottom: 70px;
  }
}

.benefits-with-us-section__image-background-wrapper {
  position: absolute;
  top: 277px;
  left: 0;
  right: 0;
  height: 2360px;

  display: flex;
  align-items: center;
  justify-content: center;

  @media screen and (min-width: 1170px) {
    top: 180px;
    height: 1093px;
  }

  img {
    width: 100%;
    height: 100%;
    display: block;
    min-width: 1170px;
    filter: blur(5px);
    transition: filter 400ms ease-in-out;
    object-fit: cover;
    object-position: 50% 50%;

    @media screen and (min-width: 1170px) {
      height: auto;
      min-width: 1920px;
    }
  }

  img.loaded {
    filter: blur(0);
  }
}

.benefits-with-us-section__container {
  position: relative;
  z-index: 1;
}

.benefits-with-us-section__title {
  color: #2e2e2e;
  text-align: center;
  font-family: 'Futura PT';
  font-size: 23px;
  font-style: normal;
  font-weight: 700;
  line-height: normal;
  margin-bottom: 40px;

  @media screen and (min-width: 1170px) {
    font-size: 40px;
    text-align: start;
  }
}

.benefits-with-us-section__item {
  border-radius: 42px;
  background: #fff;
  box-shadow: 0px 4px 25px 0px rgba(0, 0, 0, 0.09);
  padding: 40px 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;

  @media screen and (min-width: 1170px) {
    padding: 30px;
    flex-direction: row;
  }
}

.benefits-with-us-section__item:not(:last-child) {
  margin-bottom: 40px;

  @media screen and (min-width: 1170px) {
    margin-bottom: 30px;
  }
}

.benefits-with-us__image-wrapper {
  background-color: #f5f5f5;
  border-radius: 50%;
  width: 100%;
  max-width: 100px;
  height: 100px;
  margin-bottom: 20px;
  padding: 28px;

  @media screen and (min-width: 1170px) {
    max-width: 136px;
    height: 136px;
    margin-bottom: 0;
    margin-right: 20px;
    padding: 38px;
  }

  img {
    filter: blur(5px);
    transition: filter 400ms ease-in-out;
  }

  img.loaded {
    filter: blur(0);
  }
}

.benefits-with-us-section__subtitle {
  color: #2e2e2e;
  text-align: center;
  font-family: 'Futura PT';
  font-size: 18px;
  font-style: normal;
  font-weight: 700;
  line-height: normal;
  margin-bottom: 10px;

  @media screen and (min-width: 1170px) {
    font-family: Inter;
    font-size: 20px;
    text-align: start;
  }
}

.benefits-with-us-section__description {
  color: #979797;
  text-align: center;
  font-family: Inter;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 168.5%; /* 26.96px */

  @media screen and (min-width: 1170px) {
    font-size: 20px;
    text-align: start;
  }
}
