.why-us-section {
  margin-bottom: 70px;

  @media screen and (min-width: 1170px) {
    margin-bottom: 130px;
  }
}

.why-us-section__image-wrapper {
  overflow: hidden;
  position: relative;
  width: 100%;
  height: auto;
  max-width: 290px;
  min-height: 278px;
  border-radius: 60px;
  background-color: #fffdfd;
  box-shadow: 0px 4px 25px 0px rgba(0, 0, 0, 0.09);

  background-repeat: no-repeat;
  background-position: center top 23.81px;
  background-size: 174.673px;

  @media screen and (min-width: 1170px) {
    min-height: 456px;
    max-width: 475px;
    border-radius: 111px;

    background-position: center top 39px;
    background-size: 286.103px;
  }
}

.why-us-section__image-wrapper img {
  padding-left: 10px;
  padding-right: 10px;
  position: absolute;
  left: 50%;
  bottom: 0;
  transform: translateX(-50%);
  width: 100%;
  max-width: 289px;

  @media screen and (min-width: 1170px) {
    padding-left: 15px;
    padding-right: 15px;
    max-width: 473px;
  }
}

.why-us-section__image-wrapper img {
  filter: blur(5px);
  transition: filter 400ms ease-in-out;
}

.why-us-section__image-wrapper img.loaded {
  filter: blur(0);
}

.why-us-section__flex-wrapper {
  display: flex;
  flex-direction: column-reverse;

  @media screen and (min-width: 1170px) {
    position: relative;
    flex-direction: row;
    align-items: start;
  }
}

.why-us-section__content {
  margin-bottom: 40px;

  @media screen and (min-width: 1170px) {
    margin-bottom: 0;
    margin-left: 40px;
  }

  h2 {
    color: #2e2e2e;
    text-align: center;
    font-family: Futura PT;
    font-size: 23px;
    font-style: normal;
    font-weight: 700;
    line-height: normal;
    margin-bottom: 10px;

    @media screen and (min-width: 1170px) {
      font-size: 40px;
      text-align: start;
      margin-bottom: 31px;
    }
  }

  p {
    color: #979797;
    text-align: center;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 168.5%;

    @media screen and (min-width: 1170px) {
      width: 574px;
      color: #979797;
      text-align: start;
      font-size: 20px;
      line-height: 168.523%;
    }
  }
}

.why-us-section__info-text {
  display: none;

  @media screen and (min-width: 1170px) {
    display: block;
    position: absolute;
    top: 25px;
    right: -8;
    transform: rotate(90deg);

    p {
      color: #979797;
      font-family: Inter;
      font-size: 16px;
      font-style: normal;
      font-weight: 400;
      line-height: normal;
      text-transform: uppercase;
    }
  }
}

.why-us-section__icon {
  display: none;

  @media screen and (min-width: 1170px) {
    display: block;
    margin-left: auto;
  }
}

.why-us-section__icon-arrow {
  position: absolute;
  top: -4px;
  right: -4px;
}
