//bull-background-image
.markets-trade-section {
  @media screen and (min-width: 1170px) {
    position: relative;
  }

  .markets-trade-section__bull-image-container {
    display: none;

    @media screen and (min-width: 1170px) {
      z-index: -1;
      position: absolute;
      overflow: hidden;
      top: 200px;
      left: 0;
      right: 0;
      height: 790px;

      display: flex;
      align-items: center;
      justify-content: center;
    }
  }

  .markets-trade-section__bull-image {
    @media screen and (min-width: 1170px) {
      width: 100%;
      min-width: 1920px;
      height: 100%;
    }

    img {
      width: 809px;
      filter: blur(5px);
      transition: filter 400ms ease-in-out;
    }

    img.loaded {
      filter: blur(0);
    }
  }
}

.markets-trade-section__container {
  width: 100%;
  max-width: 380px;
  @media screen and (min-width: 1170px) {
    max-width: 1230px;
  }

  h2 {
    color: #2e2e2e;
    text-align: center;
    font-family: Futura PT;
    font-size: 23px;
    font-style: normal;
    font-weight: 700;
    line-height: normal;
    padding-left: 30px;
    padding-right: 30px;

    @media screen and (min-width: 1170px) {
      text-align: start;
      font-size: 40px;
    }
  }

  .markets-trade-section__text {
    margin-top: 10px;
    text-align: center;
    padding-left: 30px;
    padding-right: 30px;

    @media screen and (min-width: 1170px) {
      margin-top: 20px;
      text-align: start;
    }
  }
}

//swiper

.swiper.markets-trade-section__swiper {
  padding: 40px 0 65px;
  padding-left: 30px;
  padding-right: 30px;

  @media screen and (min-width: 1170px) {
    padding: 50px 0 75px;
    padding-left: 30px;
    padding-right: 30px;
  }

  .swiper-button-prev,
  .swiper-button-next {
    top: auto;
    bottom: 0;

    width: 26px;
    height: 26px;

    border-radius: 25px;
    background: #4768bd;

    &:after {
      font-size: 12px;
      color: #fff;
    }
  }

  .swiper-button-prev {
    left: 75px;

    @media screen and (min-width: 1170px) {
      left: auto;
      right: 164px;
    }
  }

  .swiper-button-next {
    right: 75px;

    @media screen and (min-width: 1170px) {
      right: 30px;
    }
  }

  .swiper-pagination {
    @media screen and (min-width: 1170px) {
      transform: none;
      left: auto;
      right: 70px;
    }

    .swiper-pagination-bullet-active {
      background: #4768bd;
    }
  }
}

.markets-trade-section__swiper--desktop {
  display: none;

  @media screen and (min-width: 1170px) {
    display: block;
  }
}

.markets-trade-section__swiper--mobile {
  display: block;

  @media screen and (min-width: 1170px) {
    display: none;
  }

  .cryptocurrency-chart-list {
    display: flex;
    align-items: center;
  }

  .cryptocurrency-chart-list__item:not(:last-child) {
    margin-bottom: 40px;
  }
}

.cryptocurrency-chart-list__item {
  height: auto;
  min-height: 248px;
  display: flex;
  justify-content: center;
  flex-direction: column;
  border-radius: 42px;
  background: #fff;
  box-shadow: 0px 4px 25px 0px rgba(0, 0, 0, 0.09);
  padding-left: 20px;
  padding-right: 20px;

  @media screen and (min-width: 1170px) {
    min-height: 250px;
  }

  .loader-rotation {
    width: 48px;
    height: 48px;
    border: 5px solid #86aeb3;
    border-bottom-color: #4768bd;
    border-radius: 50%;
    display: inline-block;
    box-sizing: border-box;
    animation: rotation 1s linear infinite;
  }

  @keyframes rotation {
    0% {
      transform: rotate(0deg);
    }
    100% {
      transform: rotate(360deg);
    }
  }
}

.cryptocurrency-chart-list__item-content {
  display: flex;
  align-items: start;
  margin-bottom: 10px;

  .cryptocurrency-chart-list__item-image {
    padding: 2px;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 50px;
    height: 50px;
    margin-right: 10px;

    .loader-rotation {
      width: 24px;
      height: 24px;
    }

    img {
      filter: blur(5px);
      transition: filter 400ms ease-in-out;
    }

    img.loaded {
      filter: blur(0);
    }
  }

  .cryptocurrency-chart-list__item-name {
    color: #2e2e2e;
    font-family: Inter;
    font-size: 16px;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
    margin-bottom: 5px;

    @media screen and (min-width: 1170px) {
      font-size: 18px;
    }
  }

  .cryptocurrency-chart-list__item-symbol {
    color: #979797;
    font-family: Inter;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;

    @media screen and (min-width: 1170px) {
      font-size: 18px;
    }
  }
}

.cryptocurrency-chart-canvas-container {
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  width: 100%;
  max-width: 270px;
  height: 90px;
  margin-bottom: 15px;

  @media screen and (min-width: 1170px) {
    max-width: 350px;
    height: 90px;
    margin-bottom: 10px;
  }
}

.cryptocurrency-chart-list__item-price {
  color: #2e2e2e;
  font-family: Inter;
  font-size: 16px;
  font-style: normal;
  font-weight: 600;
  line-height: normal;
  margin-bottom: 5px;

  @media screen and (min-width: 1170px) {
    font-size: 18px;
  }
}

.cryptocurrency-chart-list__item-percentage {
  font-family: Inter;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;

  @media screen and (min-width: 1170px) {
    font-size: 18px;
  }
}

.cryptocurrency-chart-list__item-percentage--green {
  color: #56be54;
}

.cryptocurrency-chart-list__item-percentage--red {
  color: #ff0734;
}
